<template>
  <div class="w-screen h-screen flex flex-col mt-12">
    <NavbarUser />

    <div class="flex flex-col items-center p-10 bg-gray-100 flex-grow">
      <div class="bg-white w-full max-w-xl p-8 rounded-lg shadow-md">
        <h2 class="text-2xl font-semibold text-[#03a980] text-center mb-6">Transaksi Pembayaran</h2>

        <!-- Form untuk pembayaran -->
        <form @submit.prevent="processTransaction">
          <div class="space-y-4">
            <div>
              <label class="block text-sm font-medium text-gray-700">Masukkan Nama Pengirim</label>
              <input v-model="namaPengirim" type="text" class="input-field w-full mb-4" placeholder="Masukkan Nama Pengirim" required />
              
              <label class="block text-sm font-medium text-gray-700">Metode Pembayaran</label>
              <select v-model="selectedPaymentMethod" class="input-field w-full" required>
                <option disabled value="">Pilih Metode Pembayaran</option>
                <option value="Transfer Bank">Transfer Bank</option>
                <option value="E-wallet">E-wallet</option>
                <option value="QRIS">QRIS</option>
              </select>
            </div>

            <!-- Transfer Bank -->
            <div v-if="selectedPaymentMethod === 'Transfer Bank'" class="space-y-4">
              <label class="block text-sm font-medium text-gray-700">Bank Tujuan</label>
              <select v-model="selectedBank" class="input-field w-full" required>
                <option disabled value="">Pilih Bank</option>
                <option value="BCA">BCA</option>
                <option value="Mandiri">Mandiri</option>
                <option value="BNI">BNI</option>
                <option value="BRI">BRI</option>
              </select>

              <div v-if="selectedBank">
                <p class="mt-2 text-sm text-gray-700">Nomor Rekening Bank: <strong>{{ dummyAccountNumber }}</strong></p>
                <p class="mt-2 text-sm text-gray-700">Jumlah Transfer: <strong>Rp {{ transferAmount }}</strong></p>
              </div>

              <div class="mt-4">
                <label for="paymentProof" class="block text-sm font-medium text-gray-700">Upload Bukti Pembayaran</label>
                <input type="file" id="paymentProof" @change="onFileChange" class="input-field w-full" required />
              </div>
            </div>

            <!-- QRIS -->
            <div v-if="selectedPaymentMethod === 'QRIS'" class="space-y-4">
              <label class="block text-sm font-medium text-gray-700">Scan QRIS</label>
              <img src="@/assets/qriscontoh.png" alt="QRIS Code" class="w-full h-auto rounded-lg mb-4" />
            </div>
          </div>

          <!-- Tombol Bayar -->
          <button type="submit" class="mt-6 w-full bg-[#03a980] text-white px-6 py-2 rounded-lg font-semibold hover:bg-[#028a66] transition">
            Bayar Sekarang
          </button>
        </form>

        <!-- Notifikasi & Tombol Download Invoice -->
        <div v-if="transactionStatus" class="mt-6 text-center">
          <p v-if="transactionStatus === 'success'" class="text-green-500">Pembayaran berhasil! Terima kasih.</p>
          <p v-if="transactionStatus === 'failed'" class="text-red-500">Pembayaran gagal. Silakan coba lagi.</p>

          <!-- Tombol Cetak Invoice -->
          <button v-if="transactionStatus === 'success'" @click="generateInvoice" class="mt-4 w-full bg-blue-500 text-white px-6 py-2 rounded-lg font-semibold hover:bg-blue-600 transition">
            Download Invoice
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavbarUser from "@/components/NavbarUser.vue";
import jsPDF from "jspdf";

export default {
  components: {
    NavbarUser,
  },
  data() {
    return {
      namaPengirim: "",
      selectedPaymentMethod: "",
      selectedBank: "",
      transactionStatus: null,
      dummyAccountNumber: "123-456-7890",
      transferAmount: "1.000.000",
      invoiceNumber: `INV-${new Date().getTime()}`, // Generate Nomor Invoice
      selectedFile: null,
    };
  },
  methods: {
    async processTransaction() {
      if (!this.namaPengirim || !this.selectedPaymentMethod) {
        alert("Silakan lengkapi semua data transaksi.");
        return;
      }

      try {
        const response = await this.fakePaymentAPI();

        if (response.success) {
          this.transactionStatus = "success";
        } else {
          this.transactionStatus = "failed";
        }
      } catch (error) {
        console.error("Terjadi kesalahan:", error);
        this.transactionStatus = "failed";
      }
    },

    onFileChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.selectedFile = file;
      }
    },

    fakePaymentAPI() {
      return new Promise((resolve) => {
        setTimeout(() => {
          const success = Math.random() > 0.5;
          resolve({ success });
        }, 2000);
      });
    },

    generateInvoice() {
      const doc = new jsPDF();

      // Header Invoice
      doc.setFontSize(18);
      doc.text("Invoice Pembayaran", 20, 20);
      doc.setFontSize(12);
      doc.text(`Nomor Invoice: ${this.invoiceNumber}`, 20, 30);
      doc.text(`Tanggal: ${new Date().toLocaleDateString()}`, 20, 40);

      // Detail Transaksi
      doc.setFontSize(14);
      doc.text("Detail Pembayaran:", 20, 55);
      doc.setFontSize(12);
      doc.text(`Nama Pengirim: ${this.namaPengirim}`, 20, 65);
      doc.text(`Metode Pembayaran: ${this.selectedPaymentMethod}`, 20, 75);
      
      if (this.selectedPaymentMethod === "Transfer Bank") {
        doc.text(`Bank: ${this.selectedBank}`, 20, 85);
        doc.text(`Nomor Rekening: ${this.dummyAccountNumber}`, 20, 95);
      }

      doc.text(`Jumlah: Rp ${this.transferAmount}`, 20, 105);

      // Footer
      doc.setFontSize(10);
      doc.text("Terima kasih atas pembayaran Anda!", 20, 120);

      // Simpan PDF
      doc.save(`Invoice_${this.invoiceNumber}.pdf`);
    }
  },
};
</script>

<style scoped>
.input-field {
  @apply p-3 border border-gray-300 rounded-lg bg-gray-100 focus:outline-none focus:ring-2 focus:ring-[#03a980];
}

button {
  @apply p-3 bg-[#03a980] text-white rounded-lg font-semibold hover:bg-[#028a66] transition;
}
</style>
